import React, { useRef } from 'react'
import ReactDOM from 'react-dom'
import { Link,useLocation } from 'react-router-dom'
import classNames from 'classnames';
import Label from '../Label/Label';
import './menu.less'
import ReactTooltip from 'react-tooltip';
import Icon from '../Icon/Icon';
import Content from '../Content/Content';
import Tooltip from '../Tooltip/Tooltip';

export default function Menu({lang,closeSideMenu}){
  const location = useLocation();
  const moreMenuRef = useRef(null);
  const docsMenuRef = useRef(null)

  const menus = [
    {id : 'pools', text : lang['pools'],link : '/pool'},
    {id : 'Futures', text : lang['Futures'],link : '/trade/futures'},
    {id : 'options', text : lang['options'],link : '/trade/options',icon : <Label text={lang['beta']} height='16' borderRadius = '10' bgColor='#CD7A37' borderColor='#CD7A37' color='#FFFFFF' fontSize='11' className='beta-icon'/>},
    {id : 'token', text : lang['token'],link : 'https://app.deri.finance/#/token'},
    {id : 'bridge', text : lang['bridge'],link : 'https://bridge.deri.finance/#/bridge'},
    {id : 'docs', text : lang['docs'],ref : docsMenuRef,subMenu : [
      {id : 'white-paper', text : lang['white-paper'] , link : 'https://docs.deri.finance/whitepaper'},
      {id : 'code-audit', text : lang['code-audit'],link : 'https://docs.deri.finance/code-audits'},
      {id : 'team', text : lang['team'],link : 'https://deri.finance/#/team'},
      {id : 'faq', text : lang['faq'],link: 'https://docs.deri.finance/general-faq'},
      {id : 'guide', text : lang['guide'],link : 'https://docs.deri.finance/'},
      {id : 'github', text : lang['github'],link : 'https://github.com/deri-finance/'}
    ]},
    {id : 'more', text : lang['more'],ref : moreMenuRef, subMenu : [
      {id : 'governance', text : lang['governance'],link : 'https://governance.deri.finance/?locale=en#governance'},
      {id : 'stats', text : lang['stats'],link : 'https://info.deri.finance/?locale=en#/info'},
      {id : 'promotional-event', text : lang['promotional-event'],link : 'https://app.deri.finance/#/trade-to-earn'},
      {id : 'retired-pool', text : lang['retired-pool'], link : 'https://app.deri.finance/?locale=en#retired'}
    ]},
    {id : 'trade-to-earn', text : lang['trade-to-earn'],icon : '',link : 'https://app.deri.finance/#/trade-to-earn' ,className : 'long'}
  ]
  const calcPosition = (position,
    currentEvent,
    currentTarget,
    refNode,
    place,
    desiredPlace,
    effect,
    offset) => {
      const rect = currentTarget.getBoundingClientRect();
      const top = rect.top + rect.height;
      return {top:top,left : rect.left}
    }
  const hideTooltip = (ref) => {
    ReactTooltip.show(ReactDOM.findDOMNode(ref.current))
  }

  return (
    <div className='menu'>
      <div className='back-icon' onClick={() => closeSideMenu && closeSideMenu(false)}>
        <Icon token='back'  width='16' height='16' />
      </div>
      {menus.map(menu => {
        const clazz = classNames('menu-item',menu.className,{selected : location.pathname === menu.link})
        return(
          <span className={clazz} key={menu.id} onClick={() => closeSideMenu && closeSideMenu(false)}>
            {menu.subMenu
            ?
              <> 
                <div className='sub-menu' data-tip 
                  ref={menu.ref}
                  data-for={menu.id}
                  // data-event='click'
                  >{menu.text}</div>
                <Tooltip id={menu.id}  place='bottom' 
                  resizeHide={false}
                  overridePosition={calcPosition}
                  event='click'
                  scrollHide={false} className='menu-tooltip' 
                  type="info" globalEventOff='click' backgroundColor='#203B60' clickable >
                    {
                      menu.subMenu.map(subMenu => <div className='sub-menu-item' key={subMenu.id}><a target='_blank' href={subMenu.link} rel='noreferrer' alt=''>{subMenu.text}</a></div>)
                    }
                </Tooltip>
              </>
              : 
              <>{menu.icon}
              {/* <a href={menu.link}>{menu.text}</a> */}
              <Link to={menu.link}>{menu.text}</Link>
              </>
            }
          </span>
          
        )
      })}
      <Content/>
    </div>
  ) 
}