import ReactTooltip from "react-tooltip";
import styled from "styled-components";

const Wrapper = styled(ReactTooltip)`
  &.__react_component_tooltip{
    padding : 0;
  }
  &.__react_component_tooltip.show {
    opacity: 1;
    width : ${props => props.width || 216}px;
    padding : ${props => props.padding};
    border-radius: ${props => props.borderRadius}px;

  }
  &.__react_component_tooltip.show::after{
    content: none;
  }
  &.__react_component_tooltip.show.place-right{
    margin: 0px;
  }
  &.__react_component_tooltip .multi-line{
    text-align : left;
  }
`

export default function Tooltip(props){
  const {effect = 'solid' ,place = 'bottom',className = 'tooltip',type = 'info',globalEventOff= 'click',backgroundColor = '#203B60',...rest} = props
  return(
    <Wrapper {...rest}  effect={effect} place={place} className={className} type={type} globalEventOff={globalEventOff} backgroundColor={backgroundColor} clickable width={props.width}>
      {props.children}
    </Wrapper>
  )
}